.navigation-tabs {
  width: 100%;
  max-width: 1200px;
  height: 48px;
  background-color: #F5F5F5;
  display: flex;
  align-items: center;
  box-sizing: border-box;
}

.tabs-container {
  display: flex;
  height: 100%;
}

.tab {
  height: 100%;
  padding: 0 24px;
  border: none;
  background-color: transparent;
  font-size: 16px;
  cursor: pointer;
  display: flex;
  align-items: center;
  transition: all 0.2s ease;
  border-bottom: 3px solid transparent;
}

.tab-active {
  background-color: white;
  color: #333;
  border-bottom: 3px solid #2B4A7A;
  font-weight: 500;
}

.tab-inactive {
  background-color: transparent;
  color: #666;
}

.tab-inactive:hover {
  background-color: rgba(255, 255, 255, 0.5);
  color: #333;
}
