.header {
  width: 100%;
  max-width: 1200px;
  height: 80px;
  background-color: #2B4A7A;
  display: flex;
  align-items: center;
  padding: 0 24px;
  box-sizing: border-box;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.header-title {
  color: white;
  font-size: 28px;
  font-weight: bold;
  margin: 0;
}

.header-right {
  display: flex;
  align-items: center;
  gap: 24px;
}

.total-games {
  display: flex;
  align-items: center;
}

.games-count {
  color: white;
  font-size: 48px;
  font-weight: bold;
}

.date-range-selector {
  display: flex;
  align-items: center;
}

.date-dropdown {
  background-color: transparent;
  color: white;
  border: 1px solid white;
  padding: 8px 12px;
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
}

.date-dropdown option {
  background-color: #2B4A7A;
  color: white;
}

.export-button {
  width: 120px;
  height: 32px;
  background-color: transparent;
  color: white;
  border: 1px solid white;
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.export-button:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.class-selector {
  margin-left: 32px;
}

.class-dropdown {
  padding: 8px 12px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.1);
  color: white;
  font-size: 14px;
  cursor: pointer;
  min-width: 180px;
}

.class-dropdown option {
  background-color: #2B4A7A;
  color: white;
}

.class-dropdown:focus {
  outline: none;
  border-color: white;
}
