.app {
  min-height: 100vh;
  background-color: #f8f9fa;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.main-content {
  width: 100%;
  max-width: 1200px;
  padding: 24px;
  box-sizing: border-box;
}

.overview-content {
  display: flex;
  flex-direction: column;
  gap: 24px;
  margin-top: 16px;
}

.overview-navigation {
  width: 100%;
  margin-bottom: 24px;
}

.overview-nav-tabs {
  display: flex;
  gap: 0;
  border-bottom: 2px solid #e5e5e5;
}

.overview-nav-tab {
  padding: 12px 24px;
  background: none;
  border: none;
  font-size: 14px;
  font-weight: 500;
  color: #666;
  cursor: pointer;
  border-bottom: 3px solid transparent;
  transition: all 0.2s ease;
  white-space: nowrap;
}

.overview-nav-tab:hover {
  color: #333;
  background-color: #f8f9fa;
}

.overview-nav-tab.active {
  color: #333;
  border-bottom-color: #666;
  font-weight: 600;
}

.remediation-section,
.trends-section,
.question-analysis-section {
  width: 100%;
}

.top-section {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.top-left {
  width: 100%;
}

.top-right {
  width: 100%;
}

/* Responsive design */
@media (max-width: 768px) {
  .top-section {
    gap: 16px;
  }
}


.reports-content {
  display: flex;
  flex-direction: column;
  gap: 32px;
  margin-top: 16px;
}

.games-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 24px;
  justify-items: center;
}

.frequently-missed-section {
  width: 100%;
}

.stats-card,
.activity-card,
.metrics-card,
.reports-card,
.export-card {
  min-height: 200px;
}

/* Reset default margins and paddings */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
